<script>
import $ from 'jquery'
import axios from 'axios'
export default {
    name: 'leadTabs',
    props: ['lead_status'],
    methods:{
        updateStatus(id){
            $("#tab1,#tab2,#tab3,#tab4,#tab5,#tab6,#tab7").removeClass('active');
            $("#tab1,#tab2,#tab3,#tab4,#tab5,#tab6,#tab7").removeClass('original-active');
            for (let index = 1; index <= id; index++) {
                $("#tab"+index).addClass('active');
                if(index == id){
                    $("#tab"+index).addClass('original-active');
                }
            }

			axios
			.post('update_lead_status',{status: id,lead_id:this.$route.params.id},{headers: {Authorization:'Bearer '+this.$storage.get('auth').token}} )
			.then( res => {
                const data = res.data
                if (data.success) {
                    console.log(data.success)
                }
			})
			.catch(err =>{
				console.log(err)
			})



        }
    }
}
</script>