<script>

import axios from 'axios'
import $ from 'jquery'


export default {
    name: 'TheHeader',
    props: ['headerType'],
    data(){
		return{
            count:0,
            notifications:{},
            LSources:{},
            LTypes:{},
            lead_ : {fname:'',lname:'',email:'',phone:'',source:'',type:'',voi:'',tradeIn:''}
        }
    },
    created(){
        this.getLeadSource()
        this.getLeadType()
        this.getNotifications()

    },
    mounted(){
        window.Echo.channel('notiChannel') //Should be Channel Name
        .listen('NotiEvent', (e) => {
            console.log(e)
            this.count = e.count
            this.notifications = e.text
        });
    },
    methods:{
        getNotifications(){
            axios
			.get('notifications',{headers: {Authorization:'Bearer '+this.$storage.get('auth').token}})
			.then( res => {
                if(res.data){
                    this.count = res.data.count
                    this.notifications = res.data.text
                }
			})
			.catch(err =>{
				console.log(err)
			})
        },
        toggleNotitab(){
            if($(".notif-tabs").hasClass("opens") === false && this.count > 0){
                this.markAsRead()
            }
            $(".notif-tabs").toggleClass("opens");
        },
        markAsRead(){
            axios
			.get('mark_as_read',{headers: {Authorization:'Bearer '+this.$storage.get('auth').token}})
			.then( res => {
                this.count = 0;
                if(res.data){
                    this.count = 0;
                }
			})
			.catch(err =>{
				console.log(err)
			})
        },
        addNewLead(){
            console.log(this.lead_)
			axios
			.post('add_new_lead',this.lead_,{headers: {Authorization:'Bearer '+this.$storage.get('auth').token,'X-Requested-With':'XMLHttpRequest'}})
			.then( res => {
                if (res.data.success) {
                    $("#addNewLeadModal").modal('hide')
                    if(window.location.pathname != '/leads'){
                        this.$router.push('/leads')
                    }else{
                        this.$root.$emit('newLead', 'added')
                    }
                    this.lead_ = {fname:'',lname:'',email:'',phone:'',source:'',type:'',voi:'',tradeIn:''}
                }
			})
			.catch(err =>{
				console.log(err)
			})
        },
        getLeadSource(){
			axios
			.get('lead_sources')
			.then( res => {
                if (res.data.success) {
                    this.LSources = res.data.success
                }
			})
			.catch(err =>{
				console.log(err)
			})
        },
        getLeadType(){
			axios
			.get('lead_types')
			.then( res => {
                if (res.data.success) {
                    this.LTypes = res.data.success
                }
			})
			.catch(err =>{
				console.log(err)
			})
        },

    }
}
</script>
