<template>
    <!-- note-area -->
    <div class="note-area">
        <div class="accordion" id="accordionExample5">
            <div class="card">
            <div class="card-header" id="headingOne5">
                <div class="mb-0">
                <button class="btn btn-link" type="button" data-toggle="collapse" data-target="#collapseOne5" aria-expanded="true" aria-controls="collapseOne5">
                    <div class="detais-title">
                    <h3><img class="img-fluid arrow-down" src="../../assets/img/arrow-down.svg" alt="">  Notes </h3>
                    </div>
                </button>
                </div>
            </div>

            <div id="collapseOne5" class="collapse show" aria-labelledby="headingOne5" data-parent="#accordionExample5">
                <div class="card-body">
                    <div class="form-group">
                        <input type="text" v-on:keyup.enter="addNote" v-model="note" class="form-control" placeholder="Start typing to add a note...">
                    </div>
                    <div class="all-notes simplebar">
                        <!-- active-Tasks -->
                        <div class="media">
                            <div class="media-body">
                                <p v-for="nt in notes" :key="nt.no_id">{{nt.no_details}}</p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            </div>
        </div>
    
    </div>
    <!-- note-area -->
</template>
<script>
import axios from 'axios'
export default {
    name: 'Notes',
    data(){
		return{
            note : '',
            notes:{}
        }
    },
    created(){
		this.getNotes()
    },
    methods:{
        addNote(){
            if(this.note != '' && this.note != undefined && this.note != 'undefined' && this.note != null ){
                axios
                .post('add_note',{note:this.note,lead_id:this.$route.params.id},{headers: {Authorization:'Bearer '+this.$storage.get('auth').token,'X-Requested-With':'XMLHttpRequest'}})
                .then( res => {
                    if (res.data.success) {
                        this.notes.push(res.data.success);
                        this.note = ''
                        this.$root.$emit('newActivity', 'added')
                    }
                })
                .catch(err =>{
                    console.log(err)
                });
            }
        },
        getNotes(){
            axios
            .get('notes/'+this.$route.params.id)
            .then( res => {
                if (res.data.success) {
                    const data = res.data
                    this.notes = data.success
                }
            })
            .catch(err =>{
                console.log(err)
            })
        },
    }
}
</script>