<template>
    <!-- Tasks-area -->
    <div class="tasks-area">
        <div class="accordion" id="accordionExample4">
            <div class="card">
            <div class="card-header" id="headingOne4">
                <div class="mb-0">
                <button class="btn btn-link" type="button" data-toggle="collapse" data-target="#collapseOne4" aria-expanded="true" aria-controls="collapseOne4">
                    <div class="detais-title">
                    <h3><img class="img-fluid arrow-down" src="../../assets/img/arrow-down.svg" alt=""> Tasks <a class="add-appoi addtask" href="#" v-on:click.stop.prevent="toggleAddTask('show')"><img class="img-fluid" src="../../assets/img/24. Close.svg" alt=""> Add Task</a></h3>
                    </div>
                </button>
                </div>
            </div>

            <div id="collapseOne4" class="collapse show" aria-labelledby="headingOne4" data-parent="#accordionExample4">
                <div class="card-body">
                <div class="all-task simplebar">
                <!-- active-Tasks -->
                <div class="media" v-for="task in lTasks" :key="task.ta_id">
                    <p class="mr-3">
                    <label class="check2">
                        <input type="checkbox">
                        <span class="checkmarks"></span>
                    </label>
                    </p>
                    <div class="media-body">
                        <h5 class="mt-0">{{task.ta_name}}</h5>
                        <p>Scheduled for {{task.ta_date | formatDate('DD MMM')}} <a href="#">Assigned to {{task.name}}</a></p>
                    </div>
                </div>
                <!-- active-Tasks -->

                <!-- active-Tasks -->
                <!-- <div class="media">
                    <p class="mr-3">
                    <label class="check2">
                        <input type="checkbox">
                        <span class="checkmarks"></span>
                    </label>
                    </p>
                    <div class="media-body">
                    <h5 class="mt-0">Sales-24 hr Follow up call and/or email</h5>
                    <p>Scheduled for 20 Apr <a href="#">Assigned to Jamil</a></p>
                    </div>
                </div> -->
                <!-- active-Tasks -->
                </div>
                
                </div>
            </div>
            </div>
        </div>
            
        <!-- add-Tasks -->
        <div class="add-tasks">
            <div class="modal-header">
                <h5 class="modal-title">Add Task</h5>
                <a class="close hide-task" data-dismiss="modal" aria-label="Close" v-on:click.stop.prevent="toggleAddTask('hide')">
                <span aria-hidden="true"><img class="img-fluid" src="../../assets/img/23. Closex.svg" alt=""></span>
                </a>
            </div>
            <form v-on:submit.prevent = "addNewTask">
            <div class="add-body">
                
                <div class="form-group">
                    <label for="inputAddress">Name this task</label>
                    <textarea class="form-control" rows="2" placeholder="Type here..." v-model="addTask.name"></textarea>
                </div>
                <div class="form-group">
                    <label for="inputAddress">Date</label>
                    <input type="date" class="form-control" placeholder="Pick your date" v-model="addTask.date">
                </div>
                <div class="form-group">
                    <label for="inputAddress">Automate tasks</label>
                    <label class="switch">
                    <input type="checkbox" v-model="addTask.automate" >
                    <span class="slider round "></span>
                    </label>
                </div>

                <div class="show-automate" v-if="addTask.automate">
                    <div class="form-row">
                        <div class="form-group col-md-4">
                        <label for="inputCity">Time</label>
                        <select class="form-control"  v-model="addTask.tHour">
                            <option value="">hours</option>
                            <option v-for="(n, index) in 12" :key="index" v-bind:value="n">{{n}}</option>
                        </select>
                        </div>
                        <div class="form-group col-md-4">
                        <label for="inputState"></label>
                        <select class="form-control"  v-model="addTask.tMin">
                            <option value="">minutes</option>
                            <option v-for="(n, index) in 60" :key="index" v-bind:value="n-1 | numberStep('2')">{{n-1 | numberStep('2')}}</option>
                        </select>
                        </div>
                        <div class="form-group col-md-4">
                        <label for="inputZip"></label>
                        <select class="form-control" v-model="addTask.tType">
                            <option value="am">am</option>
                            <option value="pm">pm</option>
                        </select>
                        </div>
                    </div>
                    <div class="form-group">
                        <label for="inputAddress2">Add a message to be sent</label>
                        <select class="form-control js-example-tags" v-model="addTask.template">
                            <option selected="selected" value="">Select a template...</option>
                            <option>Appointment Confirmation</option>
                            <option>Are you still in the Market?</option>
                            <option>Birthday Email</option>
                            <option>Birthday Message</option>
                            <option>Close text</option>
                            <option>Congratulations</option>
                        </select>
                    </div>
                </div>

                <div class="form-group">
                    <label for="inputAddress2">Assign to</label>
                    <select class="form-control" v-model="addTask.assignTo">
                        <option value="">Select user...</option>
                        <option v-for="emp in employeesList" :key="emp.id" :value="emp.id">{{emp.name}}</option>
                    </select>
                </div>                
            </div>

            <div class="modal-footer app-footer">
                <a href="#" class="add-btn" v-on:click.stop.prevent="toggleAddTask('hide')" > Back</a>
                <button type="submit" class="rem-btn"><img class="img-fluid" src="../../assets/img/23. Close.svg" alt=""> Add</button>
            </div>
            </form>
        </div>
        <!-- add-Tasks -->


    </div>
    <!-- Tasks-area -->
</template>
<script>
import axios from 'axios'
import VueDaval from 'vue-daval'
import $ from 'jquery'

export default {
    name: 'Tasks',
    props: ['defaultAssignTo','employeesList'],
    mixins: [ VueDaval ],
    data(){
		return{
            automatdd:false,
            lTasks: {},
            addTask : {automate:false,date:'',tHour:'',tMin:'',tType:'am',assignTo:'',name:'',template:'',lead_id:this.$route.params.id}
        }
    },
    vdRules: {
        addTask:  {
            date:{required: true},assignTo:{required: true},name:{required: true},
            //tHour:{required: true},tMin:{required: true}, tDtype: {required: true},template:{required: true},
        }
    },
    created(){
		this.getTasks()
    },
    methods:{
		getTasks(){
			axios
			.get('tasks/'+this.$route.params.id)
			.then( res => {
                if (res.data.success) {
                    const data = res.data
                    this.lTasks = data.success
                }
			})
			.catch(err =>{
				console.log(err)
			})
        },
        toggleAddTask(action){
            if(action == 'show'){
                $(".add-tasks").addClass("show-add-tasks");
            }else if(action == 'hide'){
                $(".add-tasks").removeClass("show-add-tasks");
            }
            
        },
        addNewTask(){
            this.$vd.addTask.$validate().then(() => {
                console.log(this.addTask);
                axios
                .post('add_task',this.addTask,{headers: {Authorization:'Bearer '+this.$storage.get('auth').token,'X-Requested-With':'XMLHttpRequest'}})
                .then( res => {
                    if (res.data.success) {
                        this.toggleAddTask('hide')
                        this.getTasks()
                        this.addTask = {automate:false,date:'',tHour:'',tMin:'',tType:'am',assignTo:'',name:'',template:'',lead_id:this.$route.params.id}
                        this.$root.$emit('newActivity', 'added')
                    }
                })
                .catch(err =>{
                    console.log(err)
                });

            }).catch(() => {

            });
        },
    }
}
</script>