<template>
    <LeadDetailContainer>
        <div class="leaddetails-left">
            <lead-profile :lead_detail="LDetail" />
            <vehicle-of-interest />
        </div>
        <!-- leaddetails-mid -->
        <div class="leaddetails-mid">
            <lead-tabs :lead_status="LDetail.l_status"/>
            <activity-area/>
            <communication/>
        </div>
        <!-- leaddetails-mid -->

        <!-- leaddetails-right -->
        <div class="leaddetails-right">
            <appointments :employeesList="allEmployees" :defaultAssignTo="assignTo" />
            <tasks :employeesList="allEmployees" :defaultAssignTo="assignTo"/>
            <notes/>
        </div>


        <!-- <lead-detail-grid :username="allEmployees"/> -->
        <!-- <AddNewLead/> -->
    </LeadDetailContainer>
</template>

<script>
import LeadDetailContainer from '../containers/LeadDetailContainer'

import LeadProfile from '../components/leads/LeadProfile'
import VehicleOfInterest from '../components/leads/VehicleOfInterest'

import LeadTabs from '../components/leads/LeadTabs'
import ActivityArea from '../components/leads/ActivityArea'
import Communication from '../components/leads/Communication'

import Appointments from '../components/leads/Appointments'
import Tasks from '../components/leads/Tasks'
import Notes from '../components/leads/Notes'


import axios from 'axios'
export default {
    name: 'LeadDetail',
    components: {
        LeadDetailContainer,
        LeadProfile,
        VehicleOfInterest,

        LeadTabs,
        ActivityArea,
        Communication,

        Appointments,
        Tasks,
        Notes
    },
    data(){
		return{
            allEmployees:'',
            assignTo:'',
            LDetail: {}
        }
    },
    created(){
        this.getAllEmployees()
        this.getLeadDetail()
    },
    methods:{
        getAllEmployees(){
			axios
			.get('all_employees',{headers: {Authorization:'Bearer '+this.$storage.get('auth').token}} )
			.then( res => {
                const data = res.data
                if (data.success) {
                    this.allEmployees = data.success
                    this.assignTo = data.user_id
                }
			})
			.catch(err =>{
				console.log(err)
			})
        },
        getLeadDetail(){
			axios
			.get('lead_detail/'+this.$route.params.id,{headers: {Authorization:'Bearer '+this.$storage.get('auth').token}})
			.then( res => {
                if (res.data.success) {
                    this.LDetail = res.data.success
                    console.log(this.LDetail)
                }
			})
			.catch(err =>{
				console.log(err)
			})
        }
    },
}
</script>