<script>
import axios from 'axios'
export default {
    name: 'ActivityArea',
    data(){
		return{
            activities:{}
        }
    },
    created(){
        let vm = this
        this.getActivities()
        this.$root.$on('newActivity', function(action) {
            if(action == 'added'){
                vm.getActivities()
            }
        })
    },
    methods:{
        getActivities(){
            axios
            .get('activity/'+this.$route.params.id)
            .then( res => {
                if (res.data.success) {
                    const data = res.data
                    this.activities = data.success
                }
            })
            .catch(err =>{
                console.log(err)
            })
        },
    }
}
</script>